late-columns: 1fr 1fr;\r\n    grid-template-rows: 1fr 1fr;\r\n    gap: 15px;\r\n    margin: 0px 30px;\r\n}\r\n.filter{\r\n    cursor: pointer;\r\n    background-color: var(--stone350);\r\n    height: 80px;\r\n    border-radius: 12px;\r\n    box-shadow: 0px 1px 6px var(--stone300) inset;\r\n    padding: 12px;\r\n\r\n    display: flex;\r\n    flex-direction: column;\r\n    justify-content: space-between;\r\n\r\n    transition: background-color .3s, box-shadow .3s;\r\n}\r\n\r\n.filter *{\r\n    transition: color .3s;\r\n    transition: background-color .3s;\r\n}\r\n\r\n.filter:nth-child(1){\r\n    --filterTheme: var(--blue-dark)\r\n}\r\n.filter:nth-child(2){\r\n    --filterTheme: var(--red-darker)\r\n}\r\n.filter:nth-child(3){\r\n    --filterTheme: var(--stone900)\r\n}\r\n.filter:nth-child(4){\r\n    --filterTheme: #eab308\r\n}\r\n\r\n.filter > div{\r\n    display: flex;\r\n    justify-content: space-between;\r\n}\r\n.filterTitle{\r\n    font-size: .9em;\r\n    font-weight: 400;\r\n}\r\n.filterCount{\r\n    font-size: 1.1em;\r\n    font-weight: 500;\r\n}\r\n.filterIcon{\r\n    background-color: var(--filterTheme);\r\n    height: 1.5em;\r\n    width: 1.5em;\r\n    border-radius: 999px;\r\n    display: flex;\r\n    justify-content: center;\r\n    align-items: center;\r\n}\r\n.filter svg{\r\n    height: 80%;\r\n    width: 80%;\r\n    color: white;\r\n}\r\n\r\n.filter.selected{\r\n    background-color: var(--filterTheme);\r\n    box-shadow: 0px 1px 6px var(--stone300);\r\n}\r\n.filter.selected p{\r\n    color: white;\r\n}\r\n.filter.selected .filterIcon{\r\n    background-color: white;\r\n}\r\n.filter.selected svg{\r\n    color: var(--filterTheme);\r\n}\r\n\r\n#filler{\r\n    flex-grow: 1;\r\n}\r\nfooter{\r\n    font-size: 1em;\r\n    text-align: center;\r\n    padding: 10px 5px;\r\n}\r\n\r\n/* ------------------------------------ - ----------------------------------- */\r\n\r\n#taskWrapper{\r\n    background-color: var(--stone050);\r\n    height: 100vh;\r\n    display: fl